@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Montserrat:wght@300;600&display=swap');

.main{
    background-color: white;
    border-radius: 20px;
    padding: 20px;
    /* width: 700px; */
    height: auto;
    position: absolute;
    top:  50%;
    left: 50%;
    transform: translate(-50%,-50%);
}
ul, li{
    padding: 0;
    margin: 0;
    list-style: none;
}
li{
    padding: 10px;
    margin: 10px;
    border: solid 1px rgba(0, 0, 0, 0.412);
    border-radius: 10px;
}
li:hover{
    background-color: rgba(232, 214, 230, 0.625);
    cursor: pointer;
}
.question{
    font-weight: 600;
    margin-bottom: 20px;
}
